#footer-area{background-color:#f2f2f2;display:flex;justify-content:space-between}.footer-info{width:800px;line-height:3}.info-area{padding:6.5% 10%}.info-top{display:flex}.info-top li{font-size:1.1rem;padding-right:1%}.info-top>li:first-child::after{content:" • ";color:#9c9b9b}.info-top>li:nth-child(1)~li:nth-child(-n+3)::after{content:" • ";color:#9c9b9b}.info-mid{display:flex;color:#707070}.info-mid>li{padding-right:1%}.info-mid>li:first-child::after{content:" | "}.info-area>span{color:#9c9b9b;font-size:.9rem}.footer-social{width:600px}.footer-sns{padding:10%}.footer-sns ul{display:flex;justify-content:space-evenly}#brand{font-weight:bold;font-size:1rem;padding:3% 1%;border-radius:30px}.btn-top{position:absolute;width:5vw;height:6vh;right:6%;border:#fff;cursor:pointer;z-index:1}.btn-top a>img{position:relative;width:100%;bottom:50%;animation:icon-ani 1.3s ease-in-out infinite}@keyframes icon-ani{0%,100%{transform:translateY(0px) scale(0.95)}50%{transform:translateY(-20px) scale(1)}}@media screen and (min-width: 481px)and (max-width: 767px){.info-area{padding:0%;margin-left:9%;padding-top:13%}.footer-sns ul{display:block}.btn-top{width:7vw;height:3.1vh;right:3%}}@media screen and (max-width: 480px){#footer-area{display:block}.info-area{padding:5% 6%}.footer-info{line-height:2;width:auto}.footer-social{width:auto}.footer-sns ul{align-items:center;gap:4%}.btn-top a>img{animation:none}}